:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.is-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  pointer-events: none;
  margin: -1px;
  position: absolute;
}

.header {
  width: 100%;
  min-height: 60px;
  height: 60px;
  color: #fff;
  background-color: #3f57b3;
  justify-content: center;
  align-items: center;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.header input {
  min-width: 300px;
}

.gallery {
  max-width: 90%;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.search-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-more {
  margin-top: 40px;
  margin-bottom: 200px;
}

